@use "src/theme" as *;

a {
  color: $link;
  cursor: pointer;
  font-weight: 900;
  text-decoration: none;
  &:hover,
  &:focus-visible {
    color: $linkHover;
    svg {
      fill: $linkHover;
    }
  }

  svg {
    fill: $link;
    height: 10px;
    margin-left: 4px;
  }
}

.button {
  position: relative;
  background-color: $buttonBGColor;
  border-radius: 28px;
  border: none;
  color: $buttonColor;
  cursor: pointer;
  min-height: 44px;
  font-family: $fontFamily;
  font-size: $fontBody2;
  font-weight: 700;
  margin-bottom: 8px;
  min-width: 120px;
  outline: none;
  padding: 4px 32px 8px;
  transition: all ease-in-out 0.2s;
  // white-space: nowrap;

  &:hover,
  &:focus {
    background-color: $buttonHover;
  }

  &:disabled {
    background-color: $darkGray;
    cursor: default;
  }
}

.scrollTop {
  @include flexColumn(center, center);
  background-color: $darkGray;
  border-radius: 50%;
  bottom: 60px;
  cursor: pointer;
  height: 48px;
  opacity: 0;
  position: fixed;
  right: 100px;
  transition: opacity 0.4s ease-in-out;
  visibility: hidden;
  width: 48px;
  z-index: 3;
  @media screen and (max-width: 650px) {
    right: 40px;
  }

  svg {
    height: auto;
    padding-top: 4px;
    transform: rotate(180deg);
    width: 40px;
    path {
      fill: $warmWhite;
    }
  }

  &:hover {
    transform: scale(1.1);
  }
  &.visible {
    opacity: 1;
    visibility: visible;
  }
}

.iconText {
  @include flexColumn();
  color: $darkBlue;
  width: 100%;
  @media screen and (max-width: 767px) {
    align-items: center;
  }

  svg {
    height: 84px;
    margin-bottom: 20px;
    max-width: 84px;
    @media screen and (max-width: 650px) {
      margin-bottom: 0px;
    }
  }
}

.legend {
  @include flexRow();
  margin-bottom: 24px;

  .item {
    @include flexRow($alignY: center, $wrap: nowrap);
    margin-bottom: 12px;

    div {
      background-color: $violet;
      border-radius: 50%;
      height: 22px;
      margin-right: 12px;
      width: 22px;
    }
  }

  .teal div {
    margin-left: 40px;
    background-color: $teal;
    @media screen and (max-width: 767px) {
      margin-left: 0;
    }
  }
}
