//Color Palette
$optumOrange: var(--palette-primary, #ff612b);
$optumOrangeHover: var(--palette-primary-hover, #eb4e1a);
$white: var(--palette-white, #ffffff);
$sky: var(--palette-sky, #d9f6fa);
$warmWhite: var(--palette-warm-white, #fbf9f4);
$darkBlue: var(--palette-dark-blue, #002677);
$darkGray: var(--palette-dark-gray, #5a5a5a);
$link: var(--palette-link, #1a75b5);
$linkHover: var(--palette-link-hover, #00396c);
$violet: var(--palette-link, #422c88);
$violetOpaque: rgba(#422c88, 0.15);
$teal: var(--palette-link, #007c89);
$tealOpaque: rgba(#007c89, 0.15);

:root {
  --palette-primary: #ff612b;
  --palette-primary-hover: #eb4e1a;
  --palette-white: #ffffff;
  --palette-sky: #d9f6fa;
  --palette-warm-white: #fbf9f4;
  --palette-dark-blue: #002677;
  --palette-dark-gray: #5a5a5a;
}

//Defaults
$borderColor: #e0e0e0;
$buttonBGColor: $darkBlue;
$buttonColor: $white;
$buttonHover: $link;
$footerBGColor: $warmWhite;
$shadow: rgba(0, 0, 0, 35%);
