@use "src/theme" as *;

.root {
  background-color: $sky;
  background-image: url("../../assets/bg.png");
  background-position: center;
  background-size: cover;
  align-items: center;
  height: 100%;

  .content {
    max-width: 550px;
  }
}

:global {
  ::-webkit-scrollbar {
    display: none;
  }
  .rsc {
    color: $darkBlue;
    width: 100%;

    .rsc-content {
      margin-top: 0;
      padding-top: 0;
      height: 480px;
    }

    .rsc-ts-bot {
      align-items: flex-start;
      margin-bottom: 12px;
    }
    .rsc-ts-image-container {
      margin-inline-end: 8px;
      width: 46px;
      padding: 0;
    }

    .rsc-ts-bubble {
      background-color: $warmWhite;
      font-family: $fontFamily;
      margin: 0;
      max-width: 320px;
    }

    .rsc-os-options {
      display: flex;
      flex-flow: row wrap;
      justify-content: flex-end;
      margin: 0;
      padding: 0;
    }

    .rsc-os-option {
      margin-inline-end: 8px;
    }

    .rsc-ts-user {
      margin-bottom: 12px;

      .rsc-ts-image-container {
        width: 0;
        margin: 0 0;
      }

      .rsc-ts-bubble {
        background-color: $sky;
      }
    }
  }
}
