//Typography
$fontFamily: "Optum Sans", "Arial", sans-serif;
$fontHeading1: 40px;
$fontHeading2: 32px;
$fontHeading3: 28px;
$fontHeading4: 24px;
$fontHeading5: 20px;
$fontBody: 18px;
$fontBody2: 16px;
$fontLabel: 14px;

:root {
  --font-familly: "Optum Sans";
}

$h1-font-sizes: (
  null: 40px,
  768px: 38px,
  480px: 34px,
);

$h2-font-sizes: (
  null: 32px,
  768px: 30px,
);

$h3-font-sizes: (
  null: 28px,
  768px: 26px,
);

$h4-font-sizes: (
  null: 24px,
  768px: 22px,
);

$h5-font-sizes: (
  null: 20px,
  480px: 19px,
);

$number-font-sizes: (
  null: 48px,
  768px: 42px,
);

$big-number-font-sizes: (
  null: 80px,
  1023px: 72px,
);
